.status-draft {
  @apply text-surface-600 border border-surface-200;
}
.status-pending {
  @apply text-blue-600 border border-blue-100;
}
.status-approved {
  @apply text-green-600 border border-green-100;
}
.status-rejected {
  @apply text-red-600 border border-red-100;
}
.status-suspended {
  @apply text-orange-600 border border-orange-100;
}
